import { NgModule} from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { ProductDispatchCrudComponent } from './product-dispatch-crud/product-dispatch-crud.component';
import { AddListComponent } from './add-list/add-list.component';
import { TABS_DEMO_ROUTES } from './product-dispatch-crud/routes';
import { AuthGuard } from 'app/shared/interceptors/auth-gaurd';
const routes : Routes = [
    {
        path : 'crud',component : ProductDispatchCrudComponent,canActivate: [AuthGuard], children : TABS_DEMO_ROUTES
    },
    
]

@NgModule({
    imports : [RouterModule.forChild(routes)],
    exports : [RouterModule]
})

export class ProductDispatchRoutingModule{}