import { Component, OnInit, Inject, ElementRef, ViewChild } from '@angular/core';
import { FormGroup, FormBuilder, Validators, ValidatorFn, AbstractControl } from '@angular/forms';
import { MasterService } from 'app/shared/services/master/master.service';
import { MatSnackBar } from '@angular/material/snack-bar';
import { VehicleService } from 'app/shared/services/master/vehicle/vehicle.service';
import { Router } from '@angular/router';
import { MatDialogRef, MAT_DIALOG_DATA } from '@angular/material/dialog';
import { MtxDialogData } from '@ng-matero/extensions';
import { ChargerService } from 'app/shared/services/charger/charger.service';
import { CountriesService } from 'app/shared/services/countries.service';
import { Observable } from 'rxjs';
import { map, startWith } from 'rxjs/operators';
import { MessageConstant} from 'app/shared/utils/message.constant';
import {ViewEncapsulation} from '@angular/core';
import { fuseAnimations } from '@fuse/animations';
import { DatePipe } from '@angular/common';
function autocompleteObjectValidator(): ValidatorFn {
  return (control: AbstractControl): { [key: string]: any } | null => {
    if (typeof control.value === 'string') {
      return { 'invalidAutocompleteObject': { value: control.value } }
    }
    return null  /* valid option selected */
  }
}
@Component({
  selector: 'app-add-nickname',
  templateUrl: './add-nickname.component.html',
  styleUrls: ['./add-nickname.component.scss'],
    encapsulation: ViewEncapsulation.None,
  animations   : fuseAnimations,
  providers:[DatePipe]
})
export class AddNicknameComponent implements OnInit {

  params: any;
  isEditMode = true;
  form: FormGroup;
  localStorage: any;
  status = false;
  countries: any;
  stateName: any;
  cities: any;
  states: any;
  pin_code_id: any;
  country_id: any;
  state_id: any;
  @ViewChild("toggleElement") ref: any;
  maxDate = new Date();
  chargerAs = false;
  selectedCharger:any;
  @ViewChild("toggleChargerAsElement") chargerAsRef: any;
  removable:boolean = true;
  constructor(private fb: FormBuilder, private chargerService: ChargerService, private snackBar: MatSnackBar,private datePipe: DatePipe,
    public dialogRef: MatDialogRef<AddNicknameComponent>, @Inject(MAT_DIALOG_DATA) public data: MtxDialogData, public countryService: CountriesService) { }

  ngOnInit() {
    this.initialize();
  }

  initialize() {

    this.localStorage = JSON.parse(localStorage.getItem('userDetails'));

    this.params = this.data;

    this.isEditMode = this.params.action == 'CREATE' ? false : true;

    this.form = this.fb.group({
      nickname: ['',[Validators.required]],
      
    });

    if (this.isEditMode) {
      this.assignValues(this.params);
    }
    //this.getCountry();
    
  }
 
  close() {

    this.dialogRef.close();
  }
  closeActionPanel(){
    this.dialogRef.close();
  }

  onYesClick() {
    if(this.form.valid){
      if(this.selectedCharger.length>0){
        let formValues = this.form.value;

        const params: any = {
          
          data: this.selectedCharger.map(v=>({id:v.id,nick_name:formValues.nick_name})),
        }
        // tslint:disable-next-line: no-//
    
        if (this.isEditMode) {
            params.loginid = this.localStorage.userId;
            params.updatedby = this.localStorage.userId;
        } 
    
        if (this.isEditMode) {
           this.chargerService.updateNickName(params).subscribe(
             (data: any) => {
               this.snackBar.open(MessageConstant.recordUpdateMessage, 'Dismiss', { duration: 5000 });
               this.dialogRef.close();
             },
             (err) => {
               this.snackBar.open(MessageConstant.errorMessage, 'Dismiss', { duration: 5000 });
             }
           )
        }
    
        this.dialogRef.close(params);
      }else{
        this.snackBar.open("No charger is selected to add nickname", 'Dismiss', { duration: 5000 });
      }

      
    }
    
  }

  getErrorMessage(form: FormGroup) {
    return form.get('email').hasError('required')
      ? 'You must enter a value'
      : form.get('email').hasError('email')
        ? 'Not a valid email'
        : '';
  }

  assignValues(params) {

    this.selectedCharger = params.data
  }

  updateStatus(event) {
    this.status = event.checked;
    this.ref.checked = this.status;
  }
  updateChargerAsStatus(event){
    this.chargerAs = event.checked;
    this.chargerAsRef.checked = this.chargerAs;
  }
  public removeSerial(i): void {
    this.selectedCharger.splice(i,1);
   
   
  }

}





